import * as React from "react";
import { Layout, Button } from "antd";
import { MergeTagsBuilderState } from "../../../models/mergeTagsBuilderState";
import { EntityMetadata } from "../../../models/entityMetadata";

const { Header, Footer, Content } = Layout;

interface IProps {
}

export default class TemplateMergeTagsBuilder extends React.Component<IProps, MergeTagsBuilderState> {
    constructor(props: IProps) {
        super(props);
        this.state = {
            metadata: []
        };
    }

    async componentDidMount() {
        await this.GetMetadata();
    }

    async GetMetadata() {
        try {
            var response = await fetch(`${Xrm.Page.context.getClientUrl()}/api/data/v8.0/EntityDefinitions?$select=DisplayName,SchemaName&$filter=LogicalName eq 'contact' or LogicalName eq 'lead'&$expand=Attributes($select=LogicalName,DisplayName)`);
            const body = await response.json();

            var metadata = new Array<EntityMetadata>();

            body.value.forEach((entityMetadat: any) => {
                metadata.push();
            });

        } catch (error) {
            console.log(error);
        }
    }

    render() {
        return (
            <div>
                <Layout>
                    <Header>Header</Header>
                    <Content>
                        <div>
                            Content
                        </div>
                        <div>
                            <Button type="link" size={'small'}>
                                добавить</Button>
                        </div>
                    </Content>
                    <Footer>
                        <Button type="primary">Сохранить</Button>
                        <Button>Закрыть</Button>
                    </Footer>
                </Layout>
            </div>);
    }
}
